@import "./_mixins";

.trmrk-address-bar {
  display: inline-flex;
}

.trmrk-address-bar .trmrk-edtbl, .trmrk-address-bar .trmrk-rdnl {
  width: 100%;
}

.trmrk-address-bar input[type=text] {
  display: inline-flex;
  cursor: pointer;
  @include padding(1, 1, 0, 1);
  @include margin(1, 1, 0, 1);
}

.trmrk-address-bar label {
  display: inline-flex;
  width: 5em;
  @include padding(1, 1, 0, 2);
  @include margin(1, 1, 0, 2);
}

.trmrk-main-address-bar {
  width: 100%;
}
